@keyframes rotate-bg-2 {
  0% {transform: rotateZ(250deg)}
  25% {transform: rotateZ(360deg)}
  75% {transform: rotateZ(360deg)}
  100% {transform: rotateZ(250deg)}
}
/* Second animation moves 180 degrees in the last 2 seconds */
@keyframes rotate-bg-1 {
  0%, 25% {
    transform: rotateZ(180deg);
  }
  50% {
    transform: rotateZ(290deg);
  }
  75%, 100% {transform: rotateZ(180deg);}
}

@keyframes rotate-bg-2-b {
  0% {transform: rotateZ(430deg)}
  100% {transform: rotateZ(540deg)}
}
/* Second animation moves 180 degrees in the last 2 seconds */
@keyframes rotate-bg-1-b {
  
  0% {transform: rotateZ(290deg)}
  100% {transform: rotateZ(180deg)}
  /* 0%, 25% {
    transform: rotateZ(180deg);
  }
  50% {
    transform: rotateZ(290deg);
  }
  75%, 100% {transform: rotateZ(180deg);} */
}

.marker {
  background: white;
  border-radius: 50%;
  height: 1.5vw;
  width: 1.5vw;
  position: absolute;
  top: 0;
  left: calc(50% - 0.75vw);
  z-index: 2;
  box-shadow: 
    0 0 .5vw .125vw #fff,  
    /* 0 0 .45vw .25vw #f0f,  */
    0 0 2vw .75vw #0ff;
}
.end {
  animation: rotate-marker 2s infinite linear;
  transform-origin: 50% 36vw;
}
@keyframes rotate-marker {
  0% {
    transform: rotateZ(250deg);
  }
  50% {transform: rotateZ(470deg);}
  100% {transform: rotateZ(250deg);}
}